@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    @apply bg-background;
    @apply text-primary;
    @apply scroll-smooth;
    @apply text-center;
  }

  h1 {
    @apply text-2xl;
  }
  h2 {
    @apply text-xl;
  }
  h3 {
    @apply text-lg;
  }
  h4 {
    @apply text-base;
  }
  h5 {
    @apply text-sm;
  }
  h6 {
    @apply text-xs;
  }

  .text-myxl {
    @apply text-my-xl-sm;  
    @apply md:text-my-xl-md;  
    @apply lg:text-my-xl-lg;    
  }
  .text-mylg {
    @apply text-my-lg-sm;  
    @apply md:text-my-lg-md;  
    @apply lg:text-my-lg-lg;    
  }
  .text-mymd {
    @apply text-my-md-sm;
    @apply md:text-my-md-md;
    @apply lg:text-my-md-lg;
  }
  .text-mysm {
    @apply text-my-sm-sm;
    @apply md:text-my-sm-md;
    @apply lg:text-my-sm-lg;
  }
}

@layer components {
  .default-design {
    @apply bg-primary;
    @apply bg-opacity-10;
    @apply border;
    @apply border-primary;
    @apply border-opacity-30;
  }
  .primary-design {
    @apply bg-primary;
    @apply text-black;
  }
  .secondary-design {
    @apply bg-primary bg-opacity-10 hover:bg-opacity-20;
  }

  .default-hover-effect {
    @apply hover:border-opacity-80;
    @apply hover:bg-opacity-20;
  }
  .default-group-hover-effect {
    @apply group-hover:border-opacity-80;
    @apply group-hover:bg-opacity-20;
  }



}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
